import { fetchError, myError } from './notifications.js';

export default function fetchCountries(searchQuery) {
  return fetch(searchQuery)
    .then(res => res.json())
    .then(
      data => {
        if (data.status === 404) {
          myError();
          return;
        } else return data;
      },
      reason => {
        fetchError();
        return;
      },
    );
}
