import { notice, error, defaults, Stack } from '@pnotify/core';
// import '@pnotify/core/dist/BrightTheme.css';
import '@pnotify/core/dist/PNotify.css';
import '@pnotify/core/dist/Material.css';
import 'material-design-icons/iconfont/material-icons.css';

defaults.styling = 'material';
defaults.icons = 'material';

export function myNotice() {
  notice({
    text: 'Too many matches found. Please, enter a more specific query',
    delay: 2000,

    stack: new Stack({
      dir1: 'down',
      dir2: 'right',
      // firstpos1: 20,
      firstpos2: 100,
      //   context: document.getElementById('results'),
    }),
  });
}

export function myError() {
  error({
    text: 'Country name not found. Please, try again.',
    delay: 1500,

    stack: new Stack({
      dir1: 'down',
      dir2: 'right',
      //   firstpos1: 20,
      firstpos2: 100,
      //   context: document.getElementById('results'),
    }),
  });
}

export function fetchError() {
  error({
    text: 'Error! Check your BASE_URL',
    delay: 2500,

    stack: new Stack({
      dir1: 'down',
      dir2: 'right',
      firstpos2: 100,
    }),
  });
}

